import React from 'react';
import styled from 'styled-components';
import { colors } from '@components/typo';
import { FaRegStar, FaStar, FaStarHalf } from 'react-icons/fa';
import ReactStars from 'react-rating-stars-component';

const Container = styled.div`
    box-shadow: 3px 12px 24px 13px rgba(0, 0, 0, 0.03);
    width: 100%;
    padding: 10px;
    background: #fff;
    margin-top: 10px;
    border-radius: 10px;
`;

const Header = styled.div`
    display: flex;
    align-items: center;
    border-bottom: 1px solid ${colors.grey};
    padding-bottom: 10px;
`;
const AvatarContainer = styled.div`
    height: 50px;
    width: 50px;
    border-radius: 30px;
    background: ${colors.grey};
`;
const UserContent = styled.div`
    flex: 1;
    margin-left: 15px;
`;
const Username = styled.div`
    font-weight: 700;
`;
const UserLocation = styled.div`
    color: ${colors.textColor2};
    font-size: 12px;
`;

const ContentTitle = styled.div`
    font-size: 16px;
    font-weight: 600;
    margin-top: 5px;
    margin-bottom: 5px;
`;
const Contents = styled.div``;

export default function ReviewFeed() {
    return (
        <Container>
            <Header>
                <AvatarContainer></AvatarContainer>
                <UserContent>
                    <Username>Dare Segun</Username>
                    <UserLocation>NG</UserLocation>
                </UserContent>
            </Header>
            <Contents>
                <ContentTitle>I don't know what to say</ContentTitle>
                <ReactStars
                    edit={false}
                    value={4}
                    count={5}
                    size={24}
                    isHalf={true}
                    emptyIcon={<FaRegStar />}
                    halfIcon={<FaStarHalf />}
                    fullIcon={<FaStar />}
                    activeColor="#ff6600"
                />
                Since when do I have to prove that I didn’t authorize a payment? I’ve never had this with any other bank
                or Visa card. And if you want to speak to anyone you have to wait at 39 mins in the queue. It was a
                fairly nominal sum but what if large amounts of money are book abroad…so I also have to contact the
                vendor???? Absurd, really … oh but they raised my limit without even asking me - so all scammers have a
                field day. Terrible bank, horrible service, no words.
            </Contents>
        </Container>
    );
}
