import React from 'react';
import styled from 'styled-components';
import { FaRegStar, FaStar, FaStarHalf } from 'react-icons/fa';
import ReactStars from 'react-rating-stars-component';
import { colors } from '@components/typo';

const Container = styled.div`
    .add_review_modal {
        width: 500px;
        min-height: 500px;
        max-height: 95vh;
        box-shadow: 3px 12px 24px 13px rgba(0, 0, 0, 0.03);
        background-color: #fff;
        border-radius: 10px;
        padding: 10px;
    }

    @media only screen and (max-width: 767px) {
        .add_review_modal {
            width: 95vw;
        }
    }
`;

const Title = styled.div`
    font-size: 16px;
    font-weight: 700;
    margin-top: 10px;
`;

const FormField = styled.input`
    height: 50px;
    width: 100%;
    background-color: ${colors.backgroundColor};
    border-radius: 10px;
    padding: 15px;
`;

const FormField2 = styled.textarea`
    height: 300px;
    width: 100%;
    background-color: ${colors.backgroundColor};
    border-radius: 15px;
    padding: 15px;
`;

const ButtonContainer = styled.div`
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: center;
`;

const SubmitButton = styled.div`
    cursor: pointer;
    width: 200px;
    height: 40px;
    display: flex;
    align-items: center;
    justify-content: center;
    background-color: ${({ cancel }) => (cancel ? colors.grey : colors.defaultColor)};
    color: ${({ cancel }) => (cancel ? colors.textColor : '#fff')};
    border-radius: 6px;
`;

export default function AddReview({ onRequestClose }) {
    const ratingChanged = (newRating) => {
        console.log(newRating);
    };
    return (
        <Container>
            <div className="add_review_modal">
                <Title>Rate your recent experience</Title>
                <ReactStars
                    value={0}
                    count={5}
                    onChange={ratingChanged}
                    size={34}
                    isHalf={true}
                    emptyIcon={<FaRegStar />}
                    halfIcon={<FaStarHalf />}
                    fullIcon={<FaStar />}
                    activeColor="#ff6600"
                />
                <Title>Give your review a title</Title>
                <FormField></FormField>
                <Title>Tell us about your experience</Title>
                <FormField2></FormField2>
                <ButtonContainer>
                    <SubmitButton>Submit</SubmitButton>
                    <SubmitButton cancel onClick={() => onRequestClose()}>
                        Cancel
                    </SubmitButton>
                </ButtonContainer>
            </div>
        </Container>
    );
}
