import React from 'react';
import styled from 'styled-components';
import Logo from './image/logo.png';
import ReactStars from 'react-rating-stars-component';
import { colors } from '@components/typo';
import Badget from './Badget';
import { FaRegStar, FaStar, FaStarHalf } from 'react-icons/fa';

const MainContainer = styled.div`
    box-shadow: 3px 12px 24px 13px rgba(0, 0, 0, 0.03);
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 15px;
    background: #fff;
    margin-top: 10px;
`;

const Container = styled.div``;

const LogoBox = styled.div`
    width: 170px;
    height: 150px;
    background-position: center !important;
    background-size: contain !important;
    background-repeat: no-repeat !important;
`;

const DetailsPage = styled.div`
    flex: 1;
    display: flex;
    flex-direction: column;
    margin-left: 15px;
`;

const CompanyName = styled.div`
    font-size: 18px;
    font-weight: 700;
`;

const ReviewDesc = styled.div`
    color: ${colors.textColor2};
    margin-top: 5px;
    font-weight: 600;
    font-size: 13px;
`;

const WebsiteLinkContainer = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    height: 100%;
    padding: 10px;
`;

const WebsiteLinkWrapper = styled.div`
    width: 100%;
    height: 60px;
    border: 3px solid ${colors.grey};
    cursor: pointer;
    display: flex;
    justify-content: center;
    flex-direction: column;
    padding: 10px;
    border-radius: 10px;
`;

const WebTitle = styled.div`
    font-weight: 700;
`;
const WebTitleDesc = styled.div`
    font-size: 12px;
`;

export default function Header() {
    const ratingChanged = (newRating) => {
        console.log(newRating);
    };

    return (
        <div className="container">
            <Container>
                <MainContainer>
                    <div className="row">
                        <div className="col-md-2">
                            <LogoBox style={{ background: `url(${Logo})` }} />
                        </div>
                        <div className="col-md-6">
                            <DetailsPage>
                                <CompanyName>Gavel NG</CompanyName>
                                <Badget />
                                <ReviewDesc>Reviews 6,634 • Great</ReviewDesc>
                                <ReactStars
                                    edit={false}
                                    value={2.5}
                                    count={5}
                                    onChange={ratingChanged}
                                    size={28}
                                    isHalf={true}
                                    emptyIcon={<FaRegStar />}
                                    halfIcon={<FaStarHalf />}
                                    fullIcon={<FaStar />}
                                    activeColor="#ff6600"
                                />
                            </DetailsPage>
                        </div>
                        <div className="col-md-4">
                            <WebsiteLinkContainer>
                                <WebsiteLinkWrapper>
                                    <WebTitle>www.gavel.ng</WebTitle>
                                    <WebTitleDesc>Visit this website</WebTitleDesc>
                                </WebsiteLinkWrapper>
                            </WebsiteLinkContainer>
                        </div>
                    </div>
                </MainContainer>
            </Container>
        </div>
    );
}
