import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
    box-shadow: 1px 1px 4px 0px rgb(0 0 0 / 25%);
    width: 100%;
    padding: 10px;
    background: #fff;
    margin-top: 10px;
    position: sticky;
    border-radius: 10px;
    top: 90px;
    margin-bottom: 10px;
`;

const Header = styled.div`
    padding-top: 10px;
    padding-bottom: 10px;
    font-size: 16px;
    font-weight: 700;
`;

const Content = styled.div``;

export default function AboutCompany() {
    return (
        <Container>
            <Header>About Gavel.ng</Header>
            <Content>
                We're open to all. Reviews are published without moderation. We show reviews chronologically, and you
                can filter by star rating, language, location, or keyword. Read about the journey of reviews on
                Trustpilot. We encourage useful, constructive feedback. Here are 8 tips for writing reviews. We fight
                fake reviews. Here’s how we safeguard our platform.
            </Content>
        </Container>
    );
}
